*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: 0.3s ease;
  max-width: 100vw;
  max-height: 100vh;
}

body{
  font-family:'monseratt',sans-serif;
}

.app{
  background-image: url('https://wallpaperboat.com/wp-content/uploads/2021/04/14/75026/sunny-day-08.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: 0.4s ease-out;
}
.app.rain{
  background-image: url('https://wallshub.net/wp-content/uploads/2021/09/Rain-Wallpaper-Laptop.jpg');
}
.app.mist{
  background-image: url('https://wallpaperaccess.com/full/30094.jpg');
}
.app.clouds{
  background-image: url('https://wallpaperaccess.com/full/1311165.jpg');
}
.app.snow{
  background-image: url('https://wallpapercave.com/wp/sS1W57t.jpg');
}
main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.75) );
  padding:25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display:block;
  width: 100%;
  padding:15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255,255,255,0.5);
  border-radius:0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0,0,0,0.2);

  color: #313131;
  font-size: 30px;
  transition: 0.4s ease;
}

.search-box .search-bar:focus {
 background-color: rgba(255,255,255,0.8);
}


.location-box .location{
  color: #fff;
  font-size: 50px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date{
  color: #fff;
  font-size: 30px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}
.weather-box{
  text-align: center;
}

.weather-box .temp{
  position:relative;
  display:inline-block;
  margin:30px auto;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;
  color: #fff;
  font-size:142px;

  font-weight: 900;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  padding: 30px 50px;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #fff;
  font-size:60px;
  font-weight:700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}